:root {
  --background-color: #282c34;
}

.App {
  background-color: var(--background-color);
  text-align: center;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: var(--background-color);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-link {
  color: #61dafb;
}

.tabsOverallStyling { 
  position: relative;
  display: block;
  height: 100%;
  width: 90%;
  transform: translate(5%, 0);
  color: black;
  background-color: #dbf264;
}

.tableBack {
  position: relative;
  display: block;
  width: 90%;
  left: 50%;
  transform: translate(-50%, 0);
}

.homeTabs {
  align-content: center;
  margin: auto;
  background-color: #dbf264;
  max-width: 900px;
  overflow-y: auto;
}

.pageheader {
  background: linear-gradient(to right, #dbf264, #FFC0CB 33.33%, white 33.33%, white 66.66%, #ed2939 66.66%),
}

.RiderModal {
  max-width: 900px;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100px;
  background-color: white;
}

.TitleRow {
  background-color: var(--background-color);
  color: white;
  align-content: center;
}

#stageSelect {
  margin: 5px;
}